<template>
  <div class="table-list-container">
    <vue-seamless-scroll
      class="warp"
      :data="listData"
      :class-option="classOption"
    >
      <el-table
        class="table-style"
        :data="listData"
        style="width: 100%"
        :show-header="showHeader"
        fit
      >
        <el-table-column
          v-for="(item, index) in columnsList"
          :key="index"
          :prop="item.value"
          :label="item.label"
          :show-overflow-tooltip="overflow"
          :width="handleWidth(item.width)"
          :align="item.align ? item.align : 'center'"
        >
        </el-table-column>
      </el-table>
    </vue-seamless-scroll>
  </div>
</template>

<script>
export default {
  props: {
    // 表格列,键值数据
    columnsList: {
      type: [Array, Object],
      required: true,
    },
    // 列表数据
    listData: {
      type: [Array, Object],
      required: true,
    },
    // 是否显示表头
    showHeader: {
      type: Boolean,
      default: false,
    },
    // 表格列内容是否超出隐藏
    overflow: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      classOption: {
        step: 0.1,
      },
    };
  },
  methods: {
    handleWidth(width) {
      if (width && width !== "") {
        return this.$cpx(width);
      }
      return "";
    },
  },
};
</script>

<style lang="scss" scoped>
.table-list-container {
  .warp {
    width: px(318);
    height: px(146);
    overflow: hidden;
  }
  .table-style {
    background: transparent;
    &::before {
      content: none;
    }
    ::v-deep .el-table__header-wrapper {
      tr {
        background: transparent;
        .el-table__cell {
          padding: 0;
          background: transparent;
          border: none;
          font-size: px(14);
          font-weight: bold;
          color: #70e1f8;
        }
      }
    }

    ::v-deep .el-table__body-wrapper {
      tr {
        background: transparent;

        .el-table__cell {
          padding: 0;
          background: transparent;
          border: none;
          width: 338px;
          font-size: px(16);
          color: #d6f7fe;
        }
      }
    }
  }
}
</style>
